/********************************************************************

	Structure

********************************************************************/
/********************************************************************
GENERAL STYLES
********************************************************************/
h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.15; }

p {
  line-height: 1.58; }

.label {
  padding: 0.3125rem 0.3125rem 0.125rem 0.3125rem;
  font-weight: bold; }

/********************************************************************
LINK STYLES
********************************************************************/
a:visited {
  color: #004e5a; }

/* Add a external link icon */
a[target="_blank"] {
  position: relative; }
  a[target="_blank"]::after {
    content: url("../../_img/external-link-alt.svg");
    position: absolute;
    top: -10px;
    margin-left: 0.3125rem;
    width: 12px; }

/* show links which are dead */
a[href="#"] {
  position: relative;
  color: red; }
  a[href="#"]::after {
    content: url("../../_img/times-circle.svg");
    position: absolute;
    top: -10px;
    margin-left: 0.3125rem;
    width: 12px; }

/* if in the menu, move icons to the left */
.categories .menu a[target="_blank"]::before {
  content: url("../../_img/external-link-alt.svg");
  display: inline-block;
  margin-right: 0.3125rem;
  width: 12px; }

.categories .menu a[target="_blank"]::after {
  content: '';
  display: none; }

.categories .menu a[href="#"]::before {
  content: url("../../_img/times-circle.svg");
  display: inline-block;
  margin-right: 0.3125rem;
  width: 12px; }

.categories .menu a[href="#"]:after {
  content: '';
  display: none; }

/* Change the colour of telephone links */
a[href^="tel:"] {
  color: orange;
  font-weight: bold;
  text-decoration: none; }

/* add a unicode telephone icon */
a[href^="tel:"]:before {
  content: "\260e";
  padding-right: 0.3125rem; }

/* Links containing an image then text */
.caption {
  text-align: center; }
  .caption img {
    display: block; }

/********************************************************************
H1, H2, H3, H4, H5 P STYLES
********************************************************************/
/********************************************************************
HEADER STYLES
********************************************************************/
header {
  margin-bottom: 2.5rem; }

footer {
  padding: 1rem;
  border-top: 1px solid #ddd; }
  footer .menu {
    display: flex;
    flex-flow: column wrap; }
    @media print, screen and (min-width: 40em) {
      footer .menu {
        flex-flow: row wrap; } }

.top-logo {
  display: block;
  margin: 0 auto;
  max-width: 170px; }
  @media print, screen and (min-width: 64em) {
    .top-logo {
      margin: 0 15px;
      max-width: 150px; } }

.strapline {
  margin-top: 10px;
  font-size: 21px;
  color: #3276AD; }
  @media print, screen and (min-width: 64em) {
    .strapline {
      float: right;
      margin-top: 40px;
      font-size: 30px; } }

.quick-links {
  margin-bottom: 60px;
  border-bottom: 1px solid #cacaca; }

/* normal horz main nav */
.top-bar {
  /*** mobile ~ tablet menu ***/ }
  @media print, screen and (min-width: 64em) {
    .top-bar .top-bar-left-custom {
      float: left; } }
  .top-bar .menu > li > a {
    border-left: 0;
    border-top-width: 5px;
    border-top-style: solid; }
  @media print, screen and (min-width: 64em) {
    .top-bar .mobile-menu-container {
      float: left; } }
  .top-bar .mobile-menu {
    margin-top: 30px;
    margin-bottom: 30px;
    padding: 0 15px;
    font-weight: 600;
    background-color: #333; }
    .top-bar .mobile-menu > li > a {
      border: 0; }

/*** off-canvas ***/
.off-canvas {
  padding: 50px 0; }
  .off-canvas .menu > li > a {
    border-left-width: 7px; }

/*** Breadcrumbs ***/
.breadcrumbs li:not(:last-child):after {
  content: "";
  margin: 0; }

.breadcrumb-arrow {
  padding: 0 .75rem;
  color: #cacaca; }

/*** Side bar master navigation ***/
.categories {
  outline: 1px solid #ccc; }
  .categories .fa {
    position: relative;
    top: -3px; }
  .categories ul li ul {
    background-color: #f3f3f3; }
  .categories ul li ul li ul {
    background-color: #fff; }
  .categories .submenu.is-accordion-submenu {
    margin: 0;
    padding: 10px 20px;
    list-style: none; }
  .categories ul li ul li ul li ul {
    background-color: #f3f3f3; }

/*** accordion arrows ***/
.is-accordion-submenu-parent > a {
  display: block;
  padding-right: 15px; }

.is-accordion-submenu-parent > a:after {
  top: 11px;
  right: 10px;
  float: right;
  position: absolute;
  margin-top: 0; }

/* level 0 */
/* level 1 */
li ul .is-accordion-submenu-parent > a:after {
  right: 1px; }

/* level 2 */
li ul li ul .is-accordion-submenu-parent > a:after {
  right: -11px; }

/********************************************************************
NAVIGATION STYLES
********************************************************************/
.parent-cat {
  font-style: italic; }

/********************************************************************
SIDEBARS & ASIDES
********************************************************************/
.submenu.is-accordion-submenu {
  margin-left: 33px; }
